<template>
    <div id="index">
        <div class="container">
            <div>
                <h1 class="my-5 title">vi-neural</h1>
                <p>Implementace neuronových sítí za pomoci knihovny Synaptic v javascriptu.<br>
                    Seminární práce na předmět Výpočetní inteligence na Jihočeské Univerzitě v Českých Budějovicích.<br>
                    Pavel Šeps, Ivan Kratochvíl
                </p>
                <router-link class="btn btn-xlg btn-success" :to="{ name: 'Network'}">
                    Spustit
                </router-link>
            </div>
            <h1 class="my-5">Poháněno pomocí</h1>
            <div class="row">
                <div class="col-md-3">
                    <a href="http://caza.la/synaptic/" class="powered-by synapticjs" target="_blank">
                        <h3>synaptic.js</h3>
                    </a>
                </div>
                <div class="col-md-3">
                    <a href="http://vuejs.org/" class="powered-by vuejs" target="_blank">
                        <h3>Vue.js</h3>
                    </a>
                </div>
                <div class="col-md-3">
                    <a href="http://visjs.org/" class="powered-by visjs" target="_blank">
                        <h3>vis.js</h3>
                    </a>
                </div>
                <div class="col-md-3">
                    <a href="http://getbootstrap.com/" class="powered-by bootstrap" target="_blank">
                        <h3>Bootstrap</h3>
                    </a>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    /**
     * Landing page
     */
    export default {
        name: 'Index'
    }
</script>

<style lang="scss">
    .title {
        font-size: 4rem;
    }

    .btn-xlg {
        font-size: 3rem;
        padding: 1rem 2rem;
    }

    .powered-by {
        position: relative;
        display: block;
        height: 300px;
        background-color: #eee;
        background-position: center center;
        background-repeat: no-repeat;
        color: inherit;

        &:hover {
            background-color: #ddd;
        }

        &.synapticjs {
            background-image: url('data:image/png;base64,');
        }

        &.vuejs {
            background-image: url('data:image/png;base64,');
        }

        &.visjs {
            background-image: url('data:image/png;base64,');
        }

        &.bootstrap {
            background-image: url('data:image/png;base64,');
        }

        h3 {
            position: absolute;
            bottom: 0;
            width: 100%;
        }
    }
</style>



